import {InputGroup, Button} from "react-bootstrap";

export const Topbar = (props) => {
    const {userInputFunc, searchFunc} = props;
    return (
        // <div>
        //     <input type={'text'} onChange={event => userInputFunc(event)}
        //            className={'form-control'}/>
        //     <button className={'btn btn-secondary'} onChange={event => searchFunc(event)}>Search!</button>
        // </div>
        <InputGroup>
            <input type={'text'} onChange={event => userInputFunc(event)}/>
            <Button variant={'secondary'} onClick={()=>(searchFunc())}>Search!</Button>
        </InputGroup>
    )
}